<template>
    <div class="successInfo">
        <h3 class="msg">{{isShow === 'true' ? signatureMsg : anonymousMsg}}</h3>
    </div>
</template>

<script>
export default {
  name: 'success',
  data() {
    return {
      isShow: false,
      signatureMsg:
        '您的署名举报已提交，后续我们将及时向您反馈举报受理、办理情况',
      anonymousMsg:
        '您的匿名举报已提交，如需跟进了解后续办理情况，您可拨打电话查询'
    };
  },
  methods: {},
  mounted() {
    this.isShow = this.$route.query.isShow;
    console.log(this.isShow);
  }
};
</script>

<style scoped>
.successInfo {
  text-align: center;
  height: 100%;
  border: 1px solid #333;
  position: relative;
}
.successInfo .msg {
  position: absolute;
  top: 48%;
  transform: translateY(-50%);
  width: 90%;
  left: 5%;
}
</style>


