<template>
<div class="wrapper">
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="infoForm" size="mini">
    <!-- <div class="blackBorder set_padding_10"> -->
        <el-form-item label="被举报人个人姓名/机构名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="被举报人所在单位" prop="company">
          <el-input v-model="ruleForm.company"></el-input>
      </el-form-item>
      <el-form-item label="被举报人职务" prop="post">
          <el-input v-model="ruleForm.post"></el-input>
      </el-form-item>
    <!-- </div> -->
    
  <!-- <div class="blackBorder set_margin_top_10 set_padding_10"> -->
    <el-form-item label="反馈问题（请详细描述）" prop="desc">
      <el-input type="textarea" v-model="ruleForm.desc"></el-input>
    </el-form-item>
  <!-- </div> -->
  <!-- <div class="blackBorder set_margin_top_10 set_padding_10"> -->
      <el-form-item label="相关佐证材料（可上传音视频、图片、文档等附件）" prop="upload">
      <el-upload
    class="set_right"
    action="https://jsonplaceholder.typicode.com/posts/"
    multiple
    :limit="3"
    :file-list="fileList">
    <el-button size="small" type="primary">添加</el-button>
      </el-upload>
      </el-form-item>
  <!-- </div> -->

  <!-- <div class="blackBorder set_margin_top_10 set_padding_10"> -->
        <el-form-item label="举报人姓名：" prop="informant">
          <el-input v-model="ruleForm.informant"></el-input>
      </el-form-item>
      <el-form-item label="举报人电话：" prop="informantTel">
          <el-input v-model="ruleForm.informantTel"></el-input>
      </el-form-item>
      <el-form-item label="举报人电子邮箱" prop="informantEmail">
          <el-input v-model="ruleForm.informantEmail"></el-input>
      </el-form-item>
    <!-- </div> -->

    <el-form-item>
      <span>提倡署名举，请您准确填写联系方式，我们将及时向您反馈举报受理、办理情况。</span>
    </el-form-item>
    <el-form-item class="set_margin_10">
        <span>上述标*部分为必填项</span>
      <el-button class="set_right" type="primary" @click="nextStep('ruleForm')">下一步</el-button>
    </el-form-item>
  </el-form>
</div>

</template>    

<script>
export default {
  name: "reportMess",
  data() {
    return {
      fileList: [],
      ruleForm: {
        name: "",
        company: "",
        post: "",
        desc: "",
        upload: "",
        informant: "",
        informantTel: "",
        informantEmail: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "请填写被举报人个人姓名/机构名称",
            trigger: "blur"
          }
        ],
        company: [
          {
            required: true,
            message: "请填写被举报人所在单位",
            trigger: "change"
          }
        ],
        desc: [
          { required: true, message: "请详细描述反馈的信息", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    nextStep(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$confirm("", "", {
            confirmButtonText: "提交",
            center: true,
            showCancelButton: false,
            showClose: false,
            customClass: "msgModal",
            confirmButtonClass: "confirmBtn"
          })
            .then(() => {
              console.log(this.ruleForm.informant);
              this.$router.push({
                path: "/success",
                query: {
                  isShow: this.ruleForm.informant ? true : false
                }
              });
            })
            .catch(() => {});
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
.wrapper {
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
  box-shadow: #eee 0px 0px 10px;
  overflow: auto;
}
.set_right {
  float: right;
}
.infoForm /deep/ .el-form-item {
  margin-bottom: 10px;
}

.msgModal /deep/ .el-button {
  width: 40%;
}
.msgModal .confirmBtn {
  width: 60%;
}
.set_margin_10 {
  margin: 10px 0;
}
</style>


