import Vue from 'vue'
import Router from 'vue-router'
import ReportHome from '@/components/ReportHome'
import ReportMess from '@/components/ReportMess'
import Success from '@/components/Success'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'reportHome',
      component: ReportHome
    },
    {
      path: '/reportMess',
      name: 'reportMess',
      component: ReportMess
    },
    {
       path: '/success',
       name: 'success',
       component: Success
    }
  ]
})



