<template>
   <div class="wrapper">
    <div class="userMess blackBorder set_padding_10">
        <div class="set_indent">{{msg}}</div>
        <ul>
            <li class="set_margin_top_5" v-for="item in userMsg" v-bind:key="item.id">
                <span>{{item.id}}、{{item.title}}：</span>
                <el-input size="mini" v-model="item.value"></el-input>
                </li>
        </ul>
    </div>
    <div class="reportMess">
        <h2>举报须知</h2>
        <ul>
           <li class="set_indent" v-bind:key="i" v-for="(item, i) in reportMessArr">{{item}}</li>
        </ul>
        <el-button class="reportBtn" type="primary" @click="reportEvent()">我要举报</el-button>
    </div>
   </div>
</template>

<script>
export default {
  name: "reportHome",
  data() {
    return {
      msg:
        "感谢您的信任，您可通过“廉洁监督码”反映我行个人或机构有关的问题，我们将及时受理您的检举控告。本系统不会采集您的个人信息，您的个人信息将受到保护，本渠道举报信息仅由信访举报专员接收查看。您也可以选择以下渠道反映问题：",
      userMsg: [
        {
          id: 1,
          title: "电话",
          value: null
        },
        {
          id: 2,
          title: "邮寄",
          value: ""
        },
        {
          id: 3,
          title: "邮箱",
          value: ""
        }
      ],
      reportMessArr: [
        "1.受理范围为针对我行干部员工或机构违犯党纪、涉嫌职务违法和职务犯罪的检举控告，如：利用信贷审批、银行采购、代理销售等资源吃拿卡要、索贿受赂；挪用客户资金（存款虚存、不入账等）；利用职务便利贪污，或者以其他手段非法占有公共财物；搞权权交易或纵容特定关系人以权谋私，进行利益输送；滥用职权、玩忽职守等失职、渎职问题；违反中央八项规定精神和“四风”方面问题等。",
        "2.举报人应做到实事求是，如实反映被举报个人/机构的有关情况和违纪违法事实。"
      ]
    };
  },
  methods: {
    reportEvent: function() {
      console.log("点击");
      this.$router.push("/reportMess");
    }
  }
};
</script>

<style scoped>
.wrapper {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.userMess {
  width: 100%;
  box-sizing: border-box;
}
.reportMess {
  text-align: center;
}
.reportMess ul li {
  text-align: left;
}
.reportBtn {
  margin-top: 60px;
  width: 80%;
}
.userMess /deep/ .el-input {
  width: inherit;
}
</style>


